body {
  font-size: 16px;
}
.eye-version-panel {
  display: none;
  padding: 28px 0;
  background: #ededed !important;
  .wrapper {
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-wrap: wrap;
  }
}
.eye-version-item {
  display: flex;
  align-items: center;

  &_back {
    cursor: pointer;
    &__text {
      margin-right: 10px;
    }
    .icon-back-svg {
      transition: @transFast;
    }
    @media (min-width: @hoverW) {
      &:hover {
        .icon-back-svg {
          transform: rotate(-30deg) translateY(6px) translateX(-2px);
        }
      }
    }
  }
}
.eye-version__color_text {
  font-size: 15px !important;
  font-weight: 700;
  color: #000 !important;
  margin-right: 10px;
  margin-left: 15px;
  display: inline-block;
  vertical-align: middle;
}
.eye-version__colors {
  display: inline-block;
  vertical-align: middle;
  font-size: 25px;
}
.eye-version__color {
  position: relative;
  display: inline-block;
  vertical-align: middle;
  background: white;
  border: 2px solid #000000;
  border-radius: 7px;
  width: 40px;
  height: 40px;
  font-weight: 700;
  text-align: center;
  color: #000 !important;
  line-height: 35px;
  box-sizing: border-box;
  font-size: 25px !important;
  margin-right: 3px;
}
.eye-version__color:hover {
  text-decoration: underline;
}
.eye-version__color[data-color='black'] {
  background: black;
  color: white !important;
  border-color: black;
}
.eye-version__color.m--current:after {
  position: absolute;
  content: '';
  width: 5px;
  height: 5px;
  bottom: -10px;
  border-radius: 50%;
  left: 50%;
  margin-left: -2px;
  background: #000;
}
.eye-version__scale_text {
  padding-left: 40px;
  font-size: 15px !important;
  line-height: 39px;
  font-weight: bold;
  color: #000 !important;
  margin-left: 162px;
  display: inline-block;
  vertical-align: top;
  margin-right: 12px;
}
.eye-version__scales {
  display: inline-block;
  vertical-align: baseline;
  margin-right: 22px;
  line-height: 20px;
}
.eye-version__scale {
  position: relative;
  display: inline-block;
  vertical-align: bottom;
  font-weight: bold;
  color: #000 !important;
  line-height: 1em;
  width: 40px;
  height: 40px;
  border: 2px solid transparent;
  line-height: 36px;
  text-align: center;
}
.eye-version__scale:hover {
  text-decoration: underline;
}
.eye-version__scale.m--current {
  border: 2px solid #000000;
  border-radius: 7px;
  width: 40px;
  height: 40px;
  line-height: 36px;
  text-align: center;
  background: #fff;
}
.eye-version__scale.m--current:after {
  position: absolute;
  content: '';
  width: 5px;
  height: 5px;
  bottom: -10px;
  border-radius: 50%;
  left: 50%;
  margin-left: -2px;
  background: #000;
}
.eye-version__scale[data-scale='1'] {
  font-size: 20px !important;
}
.eye-version__scale[data-scale='2'] {
  font-size: 25px !important;
}
.eye-version__scale[data-scale='3'] {
  font-size: 30px !important;
}
.eye-version__back {
  font-size: 15px !important;
  font-weight: bold;
  color: #000 !important;
  float: right;
  line-height: 33px;
  @media (min-width: @hoverW) {
    &:hover {
    }
  }
}
